import back from '@/components/back'
import searchCustom from '~/Pharmacy/searchCustom'
export default {
	data () {
		return {
			title: '',
			empgasisNumber: 0,
			placeholder: '请输入患者相关信息',
			arr: [],
			page: 1,
			size: 10,
			search: '',
			listStart: true,
			start404: false,
			start1: true,
			start2: true,
			start3: true,
			haveMore: true,
			PatientName: '',
			show: false,
			haveMore: true,
			Total: '',
			DrId: this.$store.state.userInfo == null ? 2 : this.$store.state.userInfo.Data.User.DoctorId
		}
	},
	components:{
		searchCustom,
		back
	},
	methods: {
		constraintLength (n) {
			return n.substring(0,4)
		},
		getIndex (id,start,PatientId, i){ //添加到我的患者
			let obj = {}
			if(start == 1 || start == '1'){
				obj.url = '/PlatFormAPI/DoctorPatient/SetRelation'
				obj.parameter = {
					DrPatientId: id,
					RelationStatus: 1
				}
			}else{
				obj.url = '/PlatFormAPI/DoctorPatient/RefuseRelation'
				obj.parameter = {
					DrPatientId: id
				}
			}
			this.$get(obj.url,obj.parameter).then(o => {
				if(o == true){ //添加成功  显示提示 重新渲染列表
					//this.$bridge.callhandler('DrPatientId', id)
					if(start == 1 || start == '1'){
						this.toIM(PatientId, i)
					}else{
						this.show = true
						this.$bridge.callhandler('rejectPatient')
						this.httpStart(this.$route.params.start)
					}
				}
			})
		},
		patientSex (start){
			switch (start) {
				case 1:
					return '男'
					break;
				case 2:
					return '女'
					break;
				case 3:
					return '未知'
					break;		
			}
		},
		// searchValue (v) { //根据搜索查询重新渲染列表
		// 	this.PatientName = v
		// 	this.httpStart(this.$route.params.start)
		// },
		searchFor(){
			this.page = 1
			this.haveMore = true
			this.httpStart(this.$route.params.start)
		},
		httpStart (start,b) { //共用页面 通过start参数区分  1：新报道患者；2：重点关注；3：分组详情； 
			if(!this.haveMore){
				return
			}
			let obj = {};
			if(start == 1){
				this.start1 = true;
				this.start2 = false;
				this.start3 = false;
				this.title = '新报道患者';
				obj.url = '/PlatFormAPI/DoctorPatient/QueryDoctorPatientPage';
				obj.parameter = {
					"DrId": this.DrId,
					"DrName": "",
					"PatientId": -1,
					"PatientName": this.search,
					"RelationStatus": -1,
					"IsDrKey": -1,
					"IsPatientMain": -1,
					"SortField": 'UpdateTime',
					"SortDirection": false,
					"PageIndex": this.page,
					"PageSize": this.size
				};
			}else if(start == 2){
				this.start1 = false;
				this.start2 = true;
				this.start3 = false;
				this.title = '重点关注';
				obj.url = '/PlatFormAPI/DoctorPatient/QueryDoctorPatientPage';
				obj.parameter = {
					"DrId": this.DrId,
					"DrName": "",
					"PatientId": -1,
					"PatientName": this.search,
					"RelationStatus": -1,
					"IsDrKey": 1,
					"IsPatientMain": -1,
					"PageIndex": this.page,
					"PageSize": this.size
				}
			}else if(start == 3){
				this.start1 = false;
				this.start2 = false;
				this.start3 = true;
				let grouapsArr = this.$store.state.grouapsArr
				if(grouapsArr.MFItemName != undefined){
					this.title = grouapsArr.MFItemName;
				}else{
					this.title = '';
				}
				obj.url = '/PlatFormAPI/Memberfeatures/QueryDrFeaturesPatientForPage'
				obj.parameter = {
					DrId: grouapsArr.DrId,
					MFItemId: grouapsArr.MFItemId,
					PatientName: this.search,
					"PageIndex": this.page,
					"PageSize": this.size
				}
			}
			this.$post(obj.url,obj.parameter).then(o => {
				if(o.ReturnData == ''  || o.ReturnData == [] || o.ReturnData == undefined){
					if(b != undefined){
						return 
					}else{
						this.start404 = true
					}
				}else{
					this.start404 = false
				}
				this.empgasisNumber = o.Total
				this.Total = o.Total
				if (!o.ReturnData || o.ReturnData.length < 10){
					this.haveMore = false
				}
				else {
					this.haveMore = true
				}
				if (b){
					o.ReturnData.forEach(v => {
						this.arr.push(v)
					})
				}
				else {
					this.arr = o.ReturnData
				}
				/*
				if(o.ReturnData == []){
					_this.listStart = true;
				}else{
					_this.arr = o.ReturnData;
				}*/
			})
		},
		toIM(i){
			this.$post('IMAPI/Message/GetNullIMRelationRecord',{
                DoctorId: this.DrId,
                PatientId: i.PatientId
            }).then(o => {
				o.Total = this.Total
				o.PatientInfo = {
					PatientId: i.PatientId,
					PatientName: i.PatientName,
					PatientSex: i.PatientSex,
					Birthday: i.Birthday,
					Age: i.Age,
				},
				o.DoctorPatientRelation = {
					DrPatientId: i.DrPatientId,
				}
				this.$bridge.callhandler('acceptPatient')
                this.$bridge.callhandler('SendMessage', JSON.stringify(o))
            })
		},
		toArc(i){
			this.$store.dispatch('pushpInfo', {
				"PatientInfo":{
					PatientId: i.PatientId,
					PatientName: i.PatientName,
					PatientSex: i.PatientSex,
					Birthday: i.Birthday,
					Age: i.Age,
				},
				"DoctorPatientRelation":{
					DrPatientId: i.DrPatientId,
				}
			})
			this.$store.dispatch('pushpatient', {
				PatientId: i.PatientId,
				PatientName: i.PatientName,
				PatientSex: i.PatientSex,
				Birthday: i.Birthday,
				Age: i.Age,
			})
			this.$router.push('/archives')
		},
		getMore(){ //下拉加载分页
			const el = this.$refs.el
			let timer = null
			
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
					|| window.pageYOffset
					|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight
					if (h - top - wh < 100 && this.haveMore){
						if(this.Total > this.size){
							this.page += 1
							this.httpStart(this.$route.params.start,true)
						}
					}
				}, 100)
			}
		}
	},
	mounted () {
		//this.httpStart(this.$route.params.start);
		let _this = this
		_this.getMore();
		_this.$bridge.registerhandler('em', (data, responseCallback) => {
			_this.httpStart(_this.$route.params.start);
			responseCallback('接收到数据')
		})
	},
	beforeDestroy(){
    window.onscroll = null
  },
	created(){
		this.httpStart(this.$route.params.start);
  }
}



